import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { FlashMessagesService } from 'flash-messages-angular';

import { AuthService } from "../../services/auth.service";
import { ValidateService } from '../../services/validate.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  userData = {
    username: "",
    email: "",
    password: "",
    confirmPassword: ""
  }

  constructor(
    private _authService: AuthService,
    private _validateService: ValidateService,
    private _flash: FlashMessagesService,
    private _router: Router
  ) { }

  ngOnInit(): void {
  }

  registerUser() {

    if (!this._validateService.validateRequiredRegister(this.userData)) {
      this._flash.show('Please fill all of fields', { cssClass: 'alert-danger', timeout: 3000 });
      return false;
    }

    if (!this._validateService.validateEmail(this.userData.email)) {
      this._flash.show('Please use a valid Email', { cssClass: 'alert-danger', timeout: 3000 });
      return false;
    }

    if (!this._validateService.validatePasswords(this.userData.password, this.userData.confirmPassword)) {
      this._flash.show('Passwords do not match', { cssClass: 'alert-danger', timeout: 3000 });
      return false;
    }

    this._authService.registerUser(this.userData)
      .subscribe(
        res => {
          
          /*
          if (!res.success) {
            this._flash.show(res.msg, { cssClass: 'alert-danger', timeout: 3000 });
            this._router.navigate(['/register']);
            return false;
          }
          localStorage.setItem('token', res.token);
          this._flash.show(res.msg, { cssClass: 'alert-success', timeout: 3000 });
          this._router.navigate(['/login']);
          return true;
          */
        },
        err => console.log(err)
      )
      
  }

}
