import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { AuthService } from "../../services/auth.service";
import { FlashMessagesService } from 'flash-messages-angular';
import { ValidateService } from '../../services/validate.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  loginUserData = {
    email: "",
    password: ""
  }

  constructor(
    private _authService: AuthService,
    private _validateService: ValidateService,
    private _router: Router,
    private _flash: FlashMessagesService
  ) { }

  ngOnInit(): void {
  }

  loginUser() {

    if (!this._validateService.validateRequiredLogin(this.loginUserData)) {
      this._flash.show('Please fill all of fields', { cssClass: 'alert-danger', timeout: 3000 });
      return false;
    }

    if (!this._validateService.validateEmail(this.loginUserData.email)) {
      this._flash.show('Please use a valid Email', { cssClass: 'alert-danger', timeout: 3000 });
      return false;
    }

    this._authService.loginUser(this.loginUserData)
      .subscribe(
        res => {
          if (!res.success) {
            this._flash.show(res.msg, { cssClass: 'alert-danger', timeout: 3000 });
            this._router.navigate(['/login']);
            return false;
          }
          this._authService.storeUserData(res.token, res.user);
          this._router.navigate(['/dashboard']);
          return true;
        },
        err => console.log(err)
      )
  }

}

